import React, { Component, useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";

import SocketClient from "../../utils/socketClient";
import Credit from "../credit";
import Scan from "../scan";
import "../styles/style.css"
import Loader from "react-loader-spinner";
import Operation from "../operation/index";
const Config = require("../../utils/config");
const api_token = "cc638af3ea2783059aae7e32b5b80e34c1f0d1f4";

const Home = ({ page, handleScan, handleCredit }) => {
  const [loginattempt, setLoginattempt] = useState(false);
  // const [page, setPage] = useState(true);
  const socket = useSelector((state) => state.socket);
  const dispatch = useDispatch();

  var isLoggingIn = document.location.href.indexOf("code=") > -1;
  useEffect(() => {
    if (!socket.loggedIn && isLoggingIn) {
      if (document.location.href.includes("code=")) {
        var params = document.location.href.split("code=")[1].split("#")[0];

        // if url has code, display the dot loader
        setLoginattempt(true);
        dispatch({ type: "OAUTH_CODE", response: params });
        (async () => {
          await SocketClient.getInstance().send("oauth", {
            params: params,
            tabId: "",
          });
          console.log("send status after oauth");
          await SocketClient.getInstance().send("status", {
            token: api_token,
            id: socket.user.id,
          });
          console.log("status afcode", params);
          await SocketClient.getInstance().send("status", {
            token: api_token,
            id: socket.user.id,
          });
        })();
      }
    } else if (socket.loggedIn) {
      // // alert()
      // SocketClient.getInstance().send("status", {
      //   token: api_token,
      //   id: socket.user.id,
      // });
    }
    if (socket && socket.isLoggingIn) {
      alert("loggedin");
      SocketClient.getInstance().send("status", {
        token: api_token,
        id: socket.user.id,
        socket_id:localStorage.get('socket_id')
      });
      window.location = Config.Config.REDIRECT_URI;
    }
  }, []);
  // const openScanPage = () =>{
  //   setPage(true)
  // }
  // const openCreditPage = () =>{
  //   setPage(false)
  // }
  const oauthLoader = () => {
    setLoginattempt(true);
  };

  return (
    <div className="container">
      {/* <div className="section">
        <h1 className="section-title"> Validate Emails</h1>
      </div> */}

      <div className="agileits-top">
        {socket.loggedIn ? (
          <div>
            <div className="row">
              <div className="col-sm-5 ">
                <Operation  handleScan = {handleScan} handleCredit = {handleCredit}/>
              </div>
              <div className="col-sm-1"></div>
              <div className="col-sm-6 ">{page ? <Scan /> : <Credit />}</div>
            </div>
          </div>
        ) : (
          <div>
            {loginattempt ? (
              <div style={{ display: "flex" }}>
                <h3 style={{ margin: "auto 20px " }}> Connecting Pipedrive</h3>
                <span>
                  <Loader
                    type="ThreeDots"
                    color="#2BAD60"
                    height="80"
                    width="80"
                  />
                </span>
              </div>
            ) : (
              <div className="box">
                <a
                  target="blank"
                  href={
                    process.env.NODE_ENV === "development"
                      ? "https://oauth.pipedrive.com/oauth/authorize?client_id=67c7097db1c334ad&redirect_uri=https://localhost:3000"
                      : "https://oauth.pipedrive.com/oauth/authorize?client_id=67c7097db1c334ad&redirect_uri=https://certalink.com/user-panel"
                  }
                  // href=
                >
                  <button className="loginButton" onClick={oauthLoader}>
                    Connect to Pipedrive
                  </button>
                </a>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default Home;

