import React, { Component, useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import { Elements, CardElement } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import SocketClient from "../../utils/socketClient";
import CheckoutForm from "../checkoutform";
import "./style.css";

const api_token = "cc638af3ea2783059aae7e32b5b80e34c1f0d1f4";
// stripe = Stripe('pk_live_3LlO84dRneuvLT4cRNrqNI1a00zer0Zu1K');
const stripePromise = loadStripe(
  "pk_test_51GsJaTENKR3DelSmluW4kP0QbP1fldW9sVMuegxwyMbmBt7yduvqb3Y0uvvNyZv9Da3gLS5o8iv25LSGvGOWYxDk009lbSNn9f"
);

const Credit = ({ props }) => {
  const dispatch = useDispatch();
  const socket = useSelector((state) => state.socket);
  const [amount, setAmount] = useState(1000);
  const [price, setPrice] = useState(8);
  const [tax, setTax] = useState(false)
  useEffect(() => {
    console.log("socket.user", socket.user)
    setTax(socket.user.locale.toLowerCase()=="en_au"?true:false)
    
  }, []);
  function handleChange(e) {
    console.log(e.target.value, getPricePlanByCredits(e.target.value).price)
    setAmount(e.target.value);
    setPrice(getPricePlanByCredits(e.target.value).price) 
    // setTax(socket.user.locale.toLowerCase()=="en_au"?true:false)
  }
  const logout = () => {
    localStorage.removeItem("loggedIn");
    localStorage.removeItem("user");
    localStorage.removeItem("access_token");
    localStorage.removeItem("status");
    window.location.reload();
  };
  const _pricePlans = {
    1000: { price: 8, description: "5K to 10K", dueTo: "40 minutes" },
    5000: { price: 39, description: "10K to 100K", dueTo: "40 minutes" },
    25000: { price: 162.5, description: "100K to 250K", dueTo: "40 minutes" },
  };
  function getPricePlanByCredits(credits) {
    for (const planIndex in _pricePlans) {
      if (planIndex == credits) {
        return _pricePlans[planIndex];
      }
    }
  }

  const ELEMENTS_OPTIONS = {
    fonts: [
      {
        cssSrc: "https://fonts.googleapis.com/css?family=Roboto",
      },
    ],
  };
  return (
    <div>
      <div>
        {/* <button className="mt-4" style={{background: '#54807e', color: 'white'}}> BUY MORE CREDITS</button> */}
      </div>

      <div className="box mt-4">
        <select
          value={amount}
          onChange={handleChange}
          style={{ width: "100%" }}
        >
          <option value="1000">1,000 credits for US $8</option>
          <option value="5000">5,000 credits for US $39</option>
          <option value="25000">25,000 credits for US $162.50</option>
        </select>
      </div>
      <div>
        <Elements stripe={stripePromise} options={ELEMENTS_OPTIONS}>
          {/* <CheckoutForm amount={amount} price={price} tax={tax} /> */}
          <CheckoutForm amount={amount} price={price} tax={tax} />

        </Elements>
      </div>
    </div>
  );
};

export default Credit;
